import React from 'react';
import {Col, Row} from "react-bootstrap";
import Jumbotron from "react-bootstrap/Jumbotron";
import Container from "react-bootstrap/Container";

export default function Footer() {
    return (
        <Jumbotron className='text-left text-light bg-dark mb-0 mt-5 font-footer p-4'>
            <Container>
                <Row>
                   <Col xs={7} sm={8} className='border-right'>
                        <p className='text-uppercase'>地址：</p>
                        <Row className='text-capitalize'>
                            <Col lg={3}>
                                <p>e2/46 Mount Wellington Highway, Mount Wellington, Auckland 1060
                                </p>
                            </Col>
                        </Row>

                    </Col>
                    <Col >
                        <p className='text-uppercase'>电话号码：</p>
                        <Row className='text-capitalize'>
                            <Col lg={3}>
                                <p> 09-570 1918
                                </p>
                            </Col>
                        </Row>

                    </Col>
                    <Col>
                        <Col>
                            <p className='text-uppercase'>营业时间</p>
                            <p>Monday — Thursday
                                <p>11:00--21:00 </p>
                            </p>

                            <p>
                                Friday — Sunday
                                <p>11:00--21:00</p>
                            </p>
                        </Col>
                    </Col>
                </Row>
            </Container>

        </Jumbotron>
    );
}