import React from 'react';
import Button from "react-bootstrap/Button";
import {Parallax} from 'react-parallax';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";

export default function CustomParallax(props) {

    const displayBtn = props.buttonText && (
        <Button className='mt-5' size="lg" variant="outline-light">{props.buttonText}</Button>);

    return (
        // <Parallax className='jumbotron jumbotron-fluid text-white shadow text-center bg-dark mb-5' bgImage={props.img} strength={400}>
        <Parallax className='jumbotron jumbotron-fluid text-white shadow text-center bg-dark mb-5' strength={400}>
            <Container style={{height: props.height}}>
                <Row className='h-100'>
                   
                    <Col className="m-auto">
                    地址：<br/><br/> e2/46 Mount Wellington Highway, Mount Wellington, Auckland 1060
                    </Col>
                    <Col className="m-auto">
                    营业时间：<br/><br/>
                    11:00--21:00
                    </Col>
                    <Col className="m-auto">
                   
                    </Col>
                    <Col className="m-auto">
                        微信扫码入群 <br/><br/>
                        <img height="150px" width="150px" src={props.img}/>
                    </Col>
                    <Col className="m-auto">
                    电话号码： 09-570 1918
                    </Col>

                        {/* <h1 className='display-4 font-weight-bold'>
                            {props.title}
                        </h1>
                        <p className='m-auto w-75'>
                            {props.text}
                        </p> */}
                        {/* {displayBtn} */}

                </Row>
            </Container>
        </Parallax>
    );
}